@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #343d4b;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
.main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #222831;
  width: 45%;
  height: 400px;
  margin: 180px auto;
  border-radius: 25px;
  color: white;
  font-family: "Montserrat", sans-serif;
  font-size: 17px;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.today-row {
  background-image: linear-gradient(135deg, #5151e5 10%, #72edf2 100%);
  width: 45%;
  border-radius: 25px;
}
.location-row {
  width: 55%;
}
.today-column {
  margin: 30px;
}
.today-column h3,
.today-column-2 h3 {
  font-size: 25px;
  font-weight: 700;
}
.today-column h4 {
  margin-top: 10px;
}
.today-column-2 {
  margin: 60px 30px 0px;
}
.today-column-2 p {
  font-size: 50px;
  font-weight: 900;
}
.today-column-2 p,
.today-column-2 h3 {
  margin-top: 10px;
}
.today-column-2 img {
  margin-left: -20px;
}
.location-column {
  text-align: center;
  margin-top: 40px;
  margin-left: 20px;
}
.location-column input {
  background-image: linear-gradient(135deg, #5151e5 10%, #72edf2 100%);
  color: white;
  border: none;
  border-radius: 20px;
  outline: none;
  height: 35px;
  width: 75%;
  padding-left: 20px;
}
.location-column input::-webkit-input-placeholder {
  color: white;
  font-weight: 700;
}
.location-column input:-ms-input-placeholder {
  color: white;
  font-weight: 700;
}
.location-column input::placeholder {
  color: white;
  font-weight: 700;
}
button {
  background-color: transparent;
  border: none;
  position: relative;
  z-index: 10;
  left: -50px;
  cursor: pointer;
  transition: 0.2s;
}
.location-details-row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 20px;
  padding: 20px;
}
.location-details-row p {
  padding: 5px;
  font-weight: 600;
}
.location-details-value {
  text-align: right;
}
.nextday-row {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin: 10px auto;
  padding: 10px;
  width: 80%;
  height: 100px;
  color: white;
  background-color: #222831;
  box-shadow: 0 0 50px -5px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  text-align: center;
  cursor: pointer;
}
.nextday-row img {
  width: 60px;
}
.nextday-row p {
  padding-bottom: 10px;
}
.nextday-column:first-child {
  background-image: linear-gradient(135deg, #5151e5 10%, #72edf2 100%);
  border-radius: 10px;
  width: 70px;
  transition: 0.5s;
}
button:hover,
.nextday-column:hover {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}
.nextday-column {
  transition: 0.5s;
  width: 70px;
  border-radius: 10px;
}
.nextday-column:hover {
  background-image: linear-gradient(135deg, #5151e5 10%, #72edf2 100%);
}
.nextday-row-p {
  font-weight: 700;
}
@media (max-width: 1370px) {
  .main {
    width: 60%;
  }
}
@media (max-width: 1050px) {
  .main {
    margin: 30px auto;
    height: 800px;
    width: 50%;
    font-size: 14px;
  }
  .today-row {
    width: 100%;
  }
  .location-row {
    width: 100%;
  }
}
@media (max-width: 800px) {
  .main {
    width: 70%;
  }
}
@media (max-width: 400px) {
  .main {
    width: 95%;
  }
}

